export default {
	props: {
		themeColor: {
			type: String,
			default () {
				return '#D1372C'
			}
		},
		menuList: {
			type: Array,
			default () {
				return []
			}
		},
		subKey: {
			type: String
		},
		
		independence: {
			type: Boolean,
			default: false
		},
		selectList: {
			type: Object
		}
	},

	data() {
		return {
			itemId: null
		}
	},
	
	computed:{
		
	},
	
	onReady() {
		// console.log(this.menuList);
		// console.log(this.subKey);
		console.log(this.selectList);
	},
	methods: {

		/**
		 * 单击选中
		 * @param {Object} item
		 */
		tapItem(item) {
			console.log(item);
			this.itemId = item.id;
			this.$emit("confirmItem", item);
		},
		
		/**
		 * 重置选中id
		 */
		resetItemId(){
			let choices = this.selectList;
			if(Object.keys(choices).length > 0){
				for(let key in choices){
					if(key === this.subKey){
						this.itemId = choices[key].id;
					}
				}
			}
		}

	}

}
